/* The Modal (background) */

.popup-clicker img {width:90px; height: auto;}


.popup {
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
	visibility:hidden;
	opacity:0;
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
	transition:all 0.2s linear 0s;
}
/* Modal Content */
.popup-content {
	position: absolute;
	top: 0;
	background-color: #fefefe;
	width: 90%;
	left: 0;
	top: 50%;
	transform: translateY(-50%);
	padding: 10px;
	right: 0;
	margin: auto;

}
/* The Close Button */

.popup-show {
visibility:visible;
opacity:1;
}
.popupclose {
  background: gray none repeat scroll 0 0;
  color: white;
  cursor: pointer;
  font-size: 13px;
  height: 30px;
  line-height: 10px; 
  padding: 10px;
  position: absolute;
  right: 10px;
  text-align: center;
  top: 10px;
  width: 30px;
  z-index: 100;
  .one {
	width: 25px;
	height: 2px;
	background-color: white;
	display: block;
	transform: rotate(45deg);
	left: 3px;
	position: absolute;
	top: 13px;
	bottom: 0;
}
  .two {
	width: 25px;
	height: 2px;
	background-color: white;
	display: block;
	transform: rotate(-45deg);
	left: 3px;
	position: absolute;
	top: 13px;
	bottom: 0;
}
}



.popup-clicker {
background: #fff;
text-align: center;
cursor: pointer;
}

@media only screen and (min-width:576px) {
.swiper-wrapper {padding-top:0;padding-bottom: 0;}    
.popup-content {
width:550px;    
height:auto;
}        
}




/* modal styles ends */